
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'


import {
jsonpReturn, //处理返回的数据
checkRequest
}
from '@/libs/util'

import {
mapGetters
} from 'vuex'

import {
AddressList,
SetAddress,
DelAddress
}
from '@/api/user'

import Record from "#/record";


export default {
data() {
return {
toastText: '', // 弹出框
toast: false,
addressList: []
}
},
components: {
Group,
XInput,
XButton,
Divider,
Flexbox,
FlexboxItem,
Toast,
Cell,
Record,
Swipeout,
SwipeoutItem,
SwipeoutButton,
},
computed: {
...mapGetters([
'username',
'telphone',
'token',
])
},
methods: {
init() {
AddressList(this.token).then((res) => {
let data = checkRequest(res)
if (data) {
this.addressList = data
console.log(this.addressList);
}
})
},
setAddress(id) {
SetAddress(this.token, id).then((res) => {
let data = checkRequest(res)
if (data) {
this.toast = true
this.toastText = data
this.init()
}
})
},
delAddress(id) {
DelAddress(this.token, id).then((res) => {
let data = checkRequest(res)
if (data) {
this.toast = true
this.toastText = data
this.init()
}
})
}

},
mounted() {
this.init()
}
}
